.header__container {
  display: flex;
  align-items: center;
}

@media screen and (max-width:500px) {
  .header__container {
    width: 100%;
    margin: 0 auto 28px;
    flex-direction: column;
    padding: 12px 0 40px;
    border-bottom: 1px solid rgba(84, 84, 84, .7);
    gap: 18px;
  }
}